@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&family=Roboto:ital,wght@1,700&display=swap');



*{
  font-family: Outfit;
}


body{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: whitesmoke;
  scroll-behavior: smooth;
}
body{

  min-height: 100vh;
}

.hero-content{
  width: 200%;
}

a{
  text-decoration: none;
  color: inherit;
}
.app{
  width: 85%;
  margin: auto;
}
@keyframes fadeIn{
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

@media screen and (max-width:770px) {
  .app{
    width: 95%;
  }
}
